import { isEmpty } from "lodash"
import { AutosuggestSource } from "@rp/react/tanstack-api/content"
import {
  SourceSelectorSelectedPackageInfo,
  SourceSelectorSelectedSourceInfo,
} from "../sourceSelector.types"
import { getIsContentElementSelectedByID } from "./getIsContentElementSelectedByID"
import { getPackageAndSourceIntersectionFunc } from "./getPackageAndSourceIntersectionFunc"

type GetIsAutosuggestSourceSelectedAnywhereFuncProps = {
  autosuggestSource: AutosuggestSource
  selectedAllPackages: Array<SourceSelectorSelectedPackageInfo>
  selectedSources: Array<SourceSelectorSelectedSourceInfo>
}

function getIsAutosuggestSourceSelectedAnywhereFunc({
  autosuggestSource,
  selectedAllPackages,
  selectedSources,
}: GetIsAutosuggestSourceSelectedAnywhereFuncProps): boolean {
  const isSourceSelectedByID = getIsContentElementSelectedByID(
    autosuggestSource.id,
    selectedSources,
  )

  if (isSourceSelectedByID) return true

  const isSourceSelectedByPackage = getPackageAndSourceIntersectionFunc(
    autosuggestSource,
    selectedAllPackages,
  )

  return !isEmpty(isSourceSelectedByPackage)
}

export { getIsAutosuggestSourceSelectedAnywhereFunc }
